import Vue from 'vue'
import axios from 'axios'
import vm from '../main.js'

// 
axios.interceptors.request.use((config) => {
  // Do ,
  // console.log('');
  vm.bLoading = true;
  return config;
}, function (error) {
  // 
  return Promise.reject(error);
});

//
axios.interceptors.response.use(function (response) {
    // console.log('');
    vm.bLoading = false;
    return response;
  }, function (error) {
    
    return Promise.reject(error);
  });


Vue.prototype.$axios = axios
window.axios = axios

